.product_container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  background-color: white;
  margin-top: 20px;
}
.product_container_box {
  width: 390px;
  margin: 10px;
  display: flex;
  border-radius: 20px ;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.product_container_box img {
 
  width: 100%;
  border-top-left-radius:20px ;
  border-top-right-radius:20px ;
  height: 340px;
  padding: 3px;  
  margin: 0px;
}
img {
  margin: 0px !important;
  padding: 0px !important;
}
.product_container_box:hover img{
 
transition: 0.2s ease-in-out;
}


.product-description{
  text-align: center;
  max-width: 75%;
    margin: auto;
    margin-top: 50px;
}
.product-description h3{
letter-spacing: 2px;
}
.btn_details{
  border :none;
  margin: 14px;
  padding: 4px;
  width: 140px;
  height: 40px;
  border: 2px solid black;
  background-color: white;
  letter-spacing: 2px;font-size:18px;

}

.btn_details:hover{
  background-color: black;
  color: white;
  cursor: pointer;
}

.btn_details span{
  cursor: pointer;
  display: inline-block;
  position: relative;
  transition: 0.5s all ease-in-out;
  margin: 4px;
}

.product_name {
  padding: 0px 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  flex-direction: column;
  color: black;

  font-weight: bold;
}
#price {
  color: lightslategray;
  align-self: flex-start;
  margin-left: 20px;
  margin-top: 5 px;
}
#rating {
  align-self: flex-start;
  margin-left: 20px;
  margin-top: 10px;
}
.btn_details span:after {
  
  content: url('../../resources/icons/Vector\ \(1\).png');
  position: absolute;
  opacity: 0;
  top: 0;
  right: -30px;
  transition: 0.5s ease-in-out;
 
  
}


.btn_details:hover span {
  padding-right: 40px;
}

.btn_details:hover span:after {
  opacity: 1;
  right: 0;
}



@media screen and (max-width: 425px) {
  .product_container_box {
    width: 100%;
    width: 132px;
    height: 200px;
  }
  .product_container_box img {
    /* width: 127px; */
    height: 200px;
    padding: 3px;
  }
  .btn_details{
   
    margin: 5px;
    padding: 3px;
     width:100px;
     height: 34px;
    border: 2px solid black;
    background-color: white;
  
     font-size: 10px;
     border-radius: 10px;
  }
}
