.register_container{
    display: flex;
    justify-content: center;
   
    align-items: center;
    width: 100%;
    height: 100%;
 
  position: relative;
}

.back_arrow{
    position: fixed;
    top: 30px;
    left:30px;
    cursor: pointer;
    color: white;
}

.back_arrow:hover{
    color: cadetblue;
}


.container_field{
    display: flex;
    flex-direction: column;
    justify-content: center;
    border-radius: 20px;
    padding:20px 50px;
    align-items: center;
    box-shadow: rgba(0, 0, 0, 0.2) 0px 20px 30px;
    margin-top: 70px;
    background-color: white;
    z-index: 100;
 
}

.container_field h2{
    margin-top: 20px;
    letter-spacing: 2px;
    word-spacing: 3px;
}
.line{
   
    border-bottom: 2px solid black;
    
    margin: 3px;
}


.container_field form{
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin: 0px 10px;
}


.btn-login{
    margin: 10px;
    padding: 4px;
    letter-spacing: 2px;
    width: 60%;
    height: 60px;
   
    outline: none;
    border: none;
}
.btn-login:hover{
    background-color: white;
    color: black;
    border: 2px solid black;
    box-shadow: rgba(0, 0, 0, 0.2) 0px 20px 30px;
}
.signin_div{
    margin: 10px 10px 20px 10px;
    text-align: center;
}

@media screen and (max-width: 425px) {

    .container_field{
        width: 100%;
        padding: 20px 0px;
    }
  .text_field{
      width: 150px;
  }


}
